import React, { Component, Fragment } from 'react';

import { Card, Row, Table, Button, Col, Menu, Icon, Form, Select, Radio, Input } from 'antd';

import Page from 'components/LayoutComponents/Page';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';

import { CreateSubject, CreateSubject2 } from 'dump/data';

import './style.scss';

const RadioGroup = Radio.Group;
const { Option } = Select;
@Form.create({ name: 'CreateSubject' })
class CreateSubjectTable extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: 1,
    };

    this.columns = [
      {
        title: 'Khóa/Mở',
        dataIndex: 'code',
        key: 'code',
        render: (text, record) => <Icon type="lock" />,
      },
      {
        title: 'Ký hiệu tên lớp TC',
        dataIndex: 'KH',
        key: 'KH',
      },
      {
        title: 'Tên môn',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Số TC',
        dataIndex: 'STC',
        key: 'STC',
      },
      {
        title: 'Hệ số',
        dataIndex: 'HS',
        key: 'HS',
      },
      {
        title: 'Số lớp',
        dataIndex: 'SL',
        key: 'SL',
      },
      {
        title: 'Số SV tối đa',
        dataIndex: 'Svmax',
        key: 'Svmax',
      },
      {
        title: 'Số SV tối thiểu',
        dataIndex: 'Svmin',
        key: 'Svmin',
      },
      {
        title: 'Số SV đ.ký',
        dataIndex: 'SvDK',
        key: 'SvDK',
      },
      {
        title: 'T.số tiết',
        dataIndex: 'TST',
        key: 'TST',
      },
      {
        title: 'S.tiết LT',
        dataIndex: 'STLT',
        key: 'STLT',
      },
      {
        title: 'S.tiết TH',
        dataIndex: 'STTH',
        key: 'STTH',
      },
      {
        title: 'S.tiết BT',
        dataIndex: 'STBT',
        key: 'STBT',
      },
      {
        title: 'S.tiết TL',
        dataIndex: 'STTL',
        key: 'STTL',
      },
      // {
      //   title: 'Thao tác',
      //   dataIndex: 'operation',
      //   render: (text, record) =>
      //     listStudents.length >= 1 ? (
      //       <Fragment>
      //         <BtnEdit href="#/profile-student" />
      //         <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
      //           <BtnDelete />
      //         </Popconfirm>
      //       </Fragment>
      //     ) : null,
      // },
    ];

    this.columns2 = [
      {
        title: 'Lớp TC',
        dataIndex: 'LTC',
        key: 'LTC',
      },
      {
        title: 'Tối thiểu',
        dataIndex: 'TIT',
        key: 'TIT',
      },
      {
        title: 'Tối đa',
        dataIndex: 'TD',
        key: 'TD',
      },
      {
        title: 'Số SV Đ.ký',
        dataIndex: 'SSV',
        key: 'SSV',
      },
      {
        title: 'Số tiết tuần',
        dataIndex: 'STT',
        key: 'STT',
      },
      {
        title: 'Thứ',
        dataIndex: 'TH',
        key: 'TH',
      },
      {
        title: 'Từ tiết',
        dataIndex: 'TT',
        key: 'TT',
      },
      {
        title: 'Đến tiết',
        dataIndex: 'DT',
        key: 'DT',
      },
      {
        title: 'Ca học',
        dataIndex: 'CH',
        // key: 'CH',
        render: (text, record) => (
          <Fragment>
            <Select defaultValue={record.CH} style={{ width: 120 }}>
              <Option value="1">Ca sáng</Option>
              <Option value="2">Ca chiều</Option>
              <Option value="3">Ca tối</Option>
            </Select>
          </Fragment>
        ),
      },
      {
        title: 'Phòng học',
        dataIndex: 'PH',
        key: 'PH',
        render: (text, record) => (
          <Fragment>
            <Select defaultValue={record.PH} style={{ width: 120 }}>
              <Option value="1">D3-dãy A</Option>
              <Option value="2">D4-dãy B</Option>
              <Option value="3">D4-dãy A</Option>
            </Select>
          </Fragment>
        ),
      },
      {
        title: 'Giáo viên',
        dataIndex: 'GV',
        // width: 300,
        key: 'GV',
        render: (text, record) => (
          <Fragment>
            <Select defaultValue={record.GV} style={{ width: 200 }}>
              <Option value="1">Nguyễn thị A</Option>
              <Option value="2">Trần văn B</Option>
              <Option value="3">Lê thị C</Option>
            </Select>
          </Fragment>
        ),
      },
      {
        title: 'Từ tuần',
        dataIndex: 'TUT',
        key: 'TUT',
      },
      {
        title: 'Đến tuần',
        dataIndex: 'DNT',
        key: 'DNT',
      },
      {
        title: 'Từ ngày',
        dataIndex: 'TN',
        key: 'TN',
      },
      {
        title: 'Đến ngày',
        dataIndex: 'DN',
        key: 'DN',
      },
    ];
    // rowSelection object indicates the need for row selection
  }

  // eslint-disable-next-line react/sort-comp
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  onChange = e => {
    console.log('radio checked', e.target.value);
    this.setState({
      value: e.target.value,
    });
  };

  render() {
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
    const { getFieldDecorator } = this.props.form;

    return (
      <Card title={this.title}>
        <Row style={this.cssBetweenRow}>
          <Col span={24}>
            <Menu
              onClick={this.handleClick}
              // selectedKeys={[this.state.current]}
              mode="horizontal"
              className="menu-item-form-brief"
            >
              <Menu.Item key="plus">
                <Icon type="plus-square" theme="twoTone" />
                <a href="#create-subject/new">Tạo môn đăng ký</a>
              </Menu.Item>
              <Menu.Item key="reload">
                <Icon type="save" />
                Lưu môn TC
              </Menu.Item>
              <Menu.Item key="reloads">
                <Icon type="book" />
                Môn tín chỉ
              </Menu.Item>
              <Menu.Item key="delete">
                <Icon type="file-add" theme="twoTone" twoToneColor="red" />
                Thêm phạm vi đăng ký
              </Menu.Item>
              <Menu.Item key="stop">
                <Icon type="stop" theme="twoTone" twoToneColor="red" />
                Thoát
              </Menu.Item>
            </Menu>
          </Col>
        </Row>
        <Form className="ant-advanced-search-form" onSubmit={this.handleSearch}>
          <Row gutter={24}>
            <Col span={8}>
              <Form.Item label="Hệ">
                {getFieldDecorator('system1')(
                  <Select>
                    <Option value="1">Đại học chính quy</Option>
                    <Option value="2">Cao đẳng chính quy</Option>
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item label="Khoa">
                {getFieldDecorator('system2', {})(
                  <Select>
                    <Option value="1">Cơ-điện tử</Option>
                    <Option value="2">Quản trị kinh doanh</Option>
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item label="Khóa">
                {getFieldDecorator('system3', {})(
                  <Select>
                    <Option value="1">Khóa 1</Option>
                    <Option value="2">Khóa 2</Option>
                    <Option value="3">Khóa 3</Option>
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item label="Ngành">
                {getFieldDecorator('system4', {})(
                  <Select>
                    <Option value="1">Cơ khí</Option>
                    <Option value="2">Điện tử viễn thông</Option>
                    <Option value="3">Điện công nghiệp</Option>
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item label="Chuyên ngành">
                {getFieldDecorator('system5', {})(
                  <Select>
                    <Option value="1">Quản trị mạng</Option>
                    <Option value="2">Lập trình web</Option>
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item label="Kỳ đăng ký">
                {getFieldDecorator('system6', {})(
                  <Select>
                    <Option value="1">Kỳ 1</Option>
                    <Option value="2">Kỳ 2</Option>
                    <Option value="2">Kỳ 3</Option>
                    <Option value="2">Kỳ 4</Option>
                    <Option value="2">Kỳ 5</Option>
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item label="Ký hiệu môn">{getFieldDecorator('system7')(<Input />)}</Form.Item>
            </Col>
            <Col span={8}>
              <Form.Item label=" ">
                {getFieldDecorator('system8')(
                  <RadioGroup onChange={this.onChange} value={this.state.value}>
                    <Radio value={1}>Tất cả các môn</Radio>
                    <Radio value={2}>Môn chung</Radio>
                  </RadioGroup>
                )}
              </Form.Item>
            </Col>
          </Row>
        </Form>
        <Row>
          <Col span={24}>
            <Table
              scroll={{ x: 1200 }}
              rowSelection={rowSelection}
              bordered
              dataSource={CreateSubject}
              columns={this.columns}
            />
          </Col>
        </Row>
        <Row style={{ marginBottom: 10 }}>
          <Col span={24}>
            <Button style={{ marginRight: 5 }} icon="save">
              Lưu TKB
            </Button>
            <Button style={{ marginRight: 5 }} icon="plus">
              Thêm lớp
            </Button>
            <Button style={{ marginRight: 5 }} icon="close">
              Xóa lớp
            </Button>
            <Button style={{ marginRight: 5 }} icon="plus">
              Thêm ca
            </Button>
            <label style={{ margin: 8 }}>B môn</label>
            <Select style={{ width: 180 }}>
              <Option value="1">Tiếng Anh</Option>
              <Option value="2">Hóa</Option>
              <Option value="3">Toán</Option>
            </Select>
            <label style={{ margin: 8 }}>Tòa nhà</label>
            <Select style={{ width: 180 }}>
              <Option value="1">Tòa nhà 1</Option>
              <Option value="2">Tòa nhà 2</Option>
              <Option value="3">Tòa nhà 3</Option>
            </Select>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              scroll={{ x: 1200 }}
              rowSelection={rowSelection}
              bordered
              dataSource={CreateSubject2}
              columns={this.columns2}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <CreateSubjectTable />
  </Page>
);



